\n    }\n}\n\n////////////////////////\n//     Navigation     //\n////////////////////////\n\n.nav {\n    display: flex;\n    justify-content: space-evenly;\n    align-items: center;\n    gap: 0.3rem;\n    list-style: none;\n    padding: 0;\n    margin: 0;\n    width: 250px;\n    min-width: 150px;\n    font-size: 1.1em;\n    .nav-item {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        gap: 0.5rem;\n        flex-direction: column;\n        height: 50px;\n        user-select: none;\n        cursor: pointer;\n        transition: transform 0.5s;\n    }\n    .nav-item:hover {\n        transform: translateY(-5px);\n    }\n    .nav-item.active {\n        transform: translateY(0);\n        color: $color5;\n    }\n}\n\n////////////////////////\n//       Footer       //\n////////////////////////\n\n#footer {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 0.4rem;\n    padding: 0.5rem;\n    font-size: 1.2em;\n    background-color: $color2;\n    color: $color1;\n    a {\n        color: $color1;\n    }\n    a:hover {\n        color: $color4;\n    }\n}","@import \"../style.scss\";\n\n////////////////////////\n//      Slideshow     //\n////////////////////////\n\n#slideshow {\n    display: flex;\n    flex-direction: column;\n    flex: 1 0 auto;\n    position: relative;\n}\n\n.slides-container {\n    position: absolute;\n    left: 0;\n    top: 0;\n    height: 100%;\n    width: 100%;\n    z-index: -1;\n    img {\n        position: absolute;\n        opacity: 0;\n        width: 100%;\n        height: 100%;\n        object-fit: cover;\n        transition: opacity 1.5s ease-in;\n    }\n    img.active {\n        opacity: 1;\n    }\n}\n\n////////////////////////\n//   slideshow-text   //\n////////////////////////\n\n.slideshow-text {\n    display: flex;\n    flex-direction: column;\n    align-self: center;\n    justify-content: center;\n    align-items: center;\n    margin: auto 1rem;\n    padding: 1rem;\n    border-radius: 1rem;\n    min-width: 50%;\n    background-color: $header-color;\n    h1 {\n        font-size: 5em;\n        color: $color5;\n    }\n}\n\n\n////////////////////////\n//       About        //\n////////////////////////\n\n#about {\n    padding: 1rem;\n    display: flex;\n    justify-content: space-evenly;\n    align-items: center;\n    gap: 1rem;\n    flex: 1 0 auto;\n    background-color: $about-color;\n    max-height: 450px;\n    .about-text {\n        text-align: center;\n        margin-top: 0.3rem;\n        width: 400px;\n        p {\n            color: $color4;\n            font-size: 1.2em;\n        }\n        h1 {\n            font-size: 3em;\n        }\n    }\n    .about-img {\n        width: 200px;\n        img {\n            width: 100%;\n            height: auto;\n            border-radius: 50%;\n            border: 1px solid $color2;\n            box-shadow: 0 1px 5px 4px $color2;\n            min-width: 100px;\n        }\n    }\n}\n\n////////////////////////\n//    Media queries   //\n////////////////////////\n\n@media screen and (max-width: 742px) {\n    #about {\n        .about-text {\n            h1 {\n                font-size: 1.8em;\n            }\n            p {\n                font-size: 1.2em;\n            }\n        }\n        .about-img {\n            width: 150px;\n        }\n    }\n\n    .slideshow-text h1 {font-size: 3em;}\n}\n\n@media screen and (max-width: 470px) {\n    #about {\n        .about-text {\n            h1 {\n                font-size: 1.4em;\n            }\n            p {\n                font-size: 1.1em;\n            }\n        }\n        .about-img {\n            width: 100px;\n        }\n    }\n    .slideshow-text h1 {font-size: 2em;}\n}\n\n\n@media screen and (max-width: 338px) {\n    #header {\n        .header-logo {\n            h1 {\n                font-size: 1.5em;\n            }\n            img {\n                width: 35px;\n            }\n        }\n        .nav {\n            font-size: 