ain-font: 'Lato', sans-serif;\n$secondary-font: 'Pacifico', cursive;\n\n* {\n    box-sizing: border-box;\n}\n\nbody {\n    font-family: $main-font;\n    margin: 0;\n    padding: 0;\n}\n\n#content {\n    display: flex;\n    flex-direction: column;\n    min-height: 100vh;\n    color: $color3;\n    a {\n        text-decoration: none;\n    }\n    h1, p {\n        margin: 0;\n    }\n    h1 {\n        font-family: $secondary-font;\n    }\n}\n\n////////////////////////\n//   Header Section   //\n////////////////////////\n\n#header {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    background-color: $header-color;\n}\n\n.header-content {\n    display: flex;\n    gap: 1rem;\n    justify-content: space-between;\n    margin: 1rem;\n    min-width: 200px;\n    width: 900px;\n}\n\n.header-logo {\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n    max-width: 150px;\n    img {\n        width: 60px;\n        min-width: 35px;\n        height: 100%;\n    }\n    h1 {\n        margin: 0;\n        font-size: 2em;\n    }\n}\n\n////////////////////////\n//     Navigation     //\n////////////////////////\n\n.nav {\n    display: flex;\n    justify-content: space-evenly;\n    align-items: center;\n    gap: 0.3rem;\n    list-style: none;\n    padding: 0;\n    margin: 0;\n    width: 250px;\n    min-width: 150px;\n    font-size: 1.1em;\n    .nav-item {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        gap: 0.5rem;\n        flex-direction: column;\n        height: 50px;\n        user-select: none;\n        cursor: pointer;\n        transition: transform 0.5s;\n    }\n    .nav-item:hover {\n        transform: translateY(-5px);\n    }\n    .nav-item.active {\n        transform: translateY(0);\n        color: $color5;\n    }\n}\n\n////////////////////////\n//       Footer       //\n////////////////////////\n\n#footer {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 0.4rem;\n    padding: 0.5rem;\n    font-size: 1.2em;\n    background-color: $color2;\n    color: $color1;\n    a {\n        color: $color1;\n    }\n    a:hover {\n        color: $color4;\n    }\n}","@import \"../style.scss\";\n\n$card-bg-color: rgba(230, 191, 194, 0.6);\n\n.content-bg {\n    display: flex;\n    flex-direction: column;\n    flex: 1 0 auto;\n    background-image: url(\"../assets/sl-wong.jpg\");\n    background-size: cover;\n    background-attachment: fixed;\n}\n\n#main {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex: 1 0 auto;\n}\n\n.contact {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n    padding: 1rem;\n    background-color: $card-bg-color;\n    width: 40%;\n    border-radius: 10%;\n    min-width: 250px;\n    margin: 2rem;\n    .logo {\n        width: 30%;\n        background-color: $color5;\n        border-radius: 50%;\n        padding: 1rem;\n        min-width: 100px;\n        max-width: 200px;\n        img {\n            width: 100%;\n            height: auto;\n        }\n    }\n    .contact-info {\n        padding: 0.5rem;\n        align-self: flex-start;\n        span {\n            color: $color5;\n            font-size: 1.1em;\n        }\n        h2 {\n            font-size: 1.3em;\n            margin: 0;\n        }\n    }\n    .social-medias {\n        display: flex;\n        justify-content: center;\n        gap: 0.4rem;\n        width: 100%;\n        margin-top: 0.8rem;\n        i {\n            color: $color5;\n        }\n        i:hover {\n            color: $color4;\n        }\n    }\n}\n\n@media screen and (max-width: 767px) {\n    .contact {\n        .contact-info {\n            h2 {font-size: 1.1em;}\n            san {font-size: 0.8em;}\n        }\n    }\n}"],"sourceRoot":""}]);
